
    <template>
      <section class="market-doc-container">
        <h2>Progress 进度条</h2>
<h3>基本使用</h3>
<mku-example>
        
        <template slot="source"><element-demo0 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div class=&quot;doc-progress-wrap&quot;&gt;
    &lt;mku-progress :percent=&quot;10&quot; class=&quot;doc-mb10&quot; /&gt;
    &lt;mku-progress :percent=&quot;30&quot; status=&quot;success&quot; class=&quot;doc-mb10&quot; /&gt;
    &lt;mku-progress :percent=&quot;50&quot; status=&quot;warning&quot; class=&quot;doc-mb10&quot; /&gt;
    &lt;mku-progress :percent=&quot;80&quot; status=&quot;error&quot; /&gt;
  &lt;/div&gt;
&lt;/template&gt;
</code></pre></template></mku-example><h3>尺寸</h3>
<mku-example>
        
        <template slot="source"><element-demo1 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div class=&quot;doc-progress-wrap&quot;&gt;
    &lt;mku-progress :percent=&quot;24&quot; :size=&quot;12&quot; color=&quot;&quot; class=&quot;doc-mb20&quot;&gt;&lt;/mku-progress&gt;
    &lt;mku-progress :percent=&quot;36&quot; :size=&quot;16&quot; class=&quot;doc-mb20&quot;&gt;&lt;/mku-progress&gt;
    &lt;mku-progress :percent=&quot;58&quot; :size=&quot;18&quot;&gt;&lt;/mku-progress&gt;
  &lt;/div&gt;
&lt;/template&gt;
</code></pre></template></mku-example><h3>颜色</h3>
<p>支持自定义颜色和更复杂的背景色渐变</p>
<mku-example>
        
        <template slot="source"><element-demo2 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div class=&quot;doc-progress-wrap&quot;&gt;
    &lt;mku-progress
      class=&quot;doc-mb20&quot;
      :percent=&quot;34&quot;
      :size=&quot;14&quot;
      :background=&quot;['to bottom', '#e2e1e1', '#777', '#e2e1e1']&quot;
      color=&quot;#4282bb&quot;
    /&gt;
    &lt;mku-progress :percent=&quot;58&quot; :size=&quot;14&quot; :color=&quot;['#f30808', '#f7f973']&quot; class=&quot;doc-mb20&quot;&gt;&lt;/mku-progress&gt;
    &lt;mku-progress :percent=&quot;68&quot; :size=&quot;14&quot; :color=&quot;['to right', '#f30808', '#f7f973']&quot; class=&quot;doc-mb20&quot;&gt;&lt;/mku-progress&gt;
    &lt;mku-progress :percent=&quot;88&quot; :size=&quot;14&quot; :color=&quot;['45deg', '#f30808', '#2dd7f5', '#f7f973']&quot; class=&quot;doc-mb20&quot;&gt;&lt;/mku-progress&gt;
  &lt;/div&gt;
&lt;/template&gt;
</code></pre></template></mku-example><h3>active动效</h3>
<mku-example>
        
        <template slot="source"><element-demo3 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div class=&quot;doc-progress-wrap&quot;&gt;
    &lt;mku-progress :percent=&quot;38&quot; :size=&quot;14&quot; active-animate=&quot;line&quot; class=&quot;doc-mb20&quot;&gt;&lt;/mku-progress&gt;
    &lt;mku-progress :percent=&quot;88&quot; :size=&quot;14&quot; active-animate=&quot;zebra&quot;&gt;&lt;/mku-progress&gt;
  &lt;/div&gt;
&lt;/template&gt;
</code></pre></template></mku-example><h3>自定义文字提示部分</h3>
<mku-example>
        
        <template slot="source"><element-demo4 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div class=&quot;doc-progress-wrap&quot;&gt;
    &lt;mku-button class=&quot;doc-mb10&quot; type=&quot;primary&quot; @click=&quot;handleAdd&quot;&gt;add 5&lt;/mku-button&gt;
    &lt;mku-progress
      :percent=&quot;percent&quot;
      :size=&quot;14&quot;
      :text-render=&quot;textRender&quot;&gt;
    &lt;/mku-progress&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
  data () {
    return {
      percent: 38
    }
  },
  methods: {
    textRender (h, percent) {
      if (percent === 100) return h('span', {
        style: { color: 'green' }
      }, '下载完成')
      return h('span', {
        style: {
          marginLeft: '10px'
        }
      }, [
        `已下载`,
        h('i', {
          style: { color: '#f00' }
        }, percent),
        '%'
      ])
    },
    handleAdd () {
      if (this.percent &gt;= 100) {
        this.percent = 100
      } else {
        this.percent += 5
      }
    }
  }
}
&lt;/script&gt;
</code></pre></template></mku-example><h3>文字提示的位置</h3>
<mku-example>
        
        <template slot="source"><element-demo5 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div class=&quot;doc-progress-wrap&quot;&gt;
    &lt;mku-progress :percent=&quot;28&quot; :size=&quot;14&quot; class=&quot;doc-mb20&quot; text-placement=&quot;end&quot;/&gt;
    &lt;mku-progress :percent=&quot;48&quot; :size=&quot;14&quot; class=&quot;doc-mb20&quot; text-placement=&quot;start&quot;/&gt;
    &lt;mku-progress :percent=&quot;68&quot; :size=&quot;20&quot; class=&quot;doc-mb20&quot; text-placement=&quot;end-inside&quot;/&gt;
    &lt;mku-progress :percent=&quot;88&quot; :size=&quot;20&quot; class=&quot;doc-mb30&quot; text-placement=&quot;start-inside&quot;/&gt;
    &lt;mku-progress :percent=&quot;50&quot; :size=&quot;20&quot; class=&quot;doc-mb30&quot; text-placement=&quot;top-follow&quot; :auto-success=&quot;false&quot;/&gt;
    &lt;mku-progress :percent=&quot;100&quot; :size=&quot;20&quot; class=&quot;doc-mb30&quot; text-placement=&quot;top-follow&quot; :auto-success=&quot;false&quot;/&gt;
    &lt;mku-progress :percent=&quot;100&quot; :size=&quot;20&quot; text-placement=&quot;top-follow&quot; /&gt;
  &lt;/div&gt;
&lt;/template&gt;
</code></pre></template></mku-example><h3>Progress Attributes</h3>
<table>
<thead>
<tr>
<th style="text-align:left">参数</th>
<th style="text-align:left">说明</th>
<th style="text-align:center">类型</th>
<th style="text-align:center">可选值</th>
<th style="text-align:center">默认值</th>
<th style="text-align:center">必填</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">percent</td>
<td style="text-align:left">百分比</td>
<td style="text-align:center"><code>Number</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center"><code>0 ~ 100</code></td>
<td style="text-align:center">是</td>
</tr>
<tr>
<td style="text-align:left">status</td>
<td style="text-align:left">进度条状态</td>
<td style="text-align:center"><code>String</code></td>
<td style="text-align:center"><code>active | success | warning | error</code></td>
<td style="text-align:center"><code>active</code></td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">auto-success</td>
<td style="text-align:left"><code>percent</code>为<code>100</code>时，自动将<code>status</code>切换为<code>success</code></td>
<td style="text-align:center"><code>Boolean</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center"><code>true</code></td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">color</td>
<td style="text-align:left">进度条颜色，支持复杂的渐变色</td>
<td style="text-align:center"><code>String | Array</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center">-</td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">background</td>
<td style="text-align:left">进度条背景色，支持复杂的渐变色</td>
<td style="text-align:center"><code>String | Array</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center">-</td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">active-animate</td>
<td style="text-align:left">进度条active时的动画效果</td>
<td style="text-align:center"><code>String</code></td>
<td style="text-align:center"><code>none | zebra | line</code></td>
<td style="text-align:center"><code>none</code></td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">size</td>
<td style="text-align:left">进度大小尺寸，单位<code>px</code></td>
<td style="text-align:center"><code>Number</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center"><code>0</code></td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">text-hidden</td>
<td style="text-align:left">隐藏进度条提示部分</td>
<td style="text-align:center"><code>Number</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center"><code>3000</code></td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">text-render</td>
<td style="text-align:left">自定义提示部分内容，接收render函数</td>
<td style="text-align:center"><code>Function</code></td>
<td style="text-align:center">-</td>
<td style="text-align:center">-</td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">text-placement</td>
<td style="text-align:left">提示部分所在的位置</td>
<td style="text-align:center"><code>String</code></td>
<td style="text-align:center"><code>start | end | start-inside | end-inside | top-follow</code></td>
<td style="text-align:center"><code>end</code></td>
<td style="text-align:center">否</td>
</tr>
</tbody>
</table>

      </section>
